.CreateButton{
    border:none;
    border-radius:50px;
    cursor:pointer;
    font-size:50px;
    position: fixed;
    bottom:24px;
    right: 24px;
    background-color: white;
    color:purple;
    font-weight: bold;
    display:flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width:64px;
    z-index:1;

    transform:rotate(0);
    transition:.3s ease;


}
.CreateButton:hover{
    transform: rotate(224deg);
}